<template>
	<!--  ref="swiper" -->
	<swiper :options="swiperOption" ref="swiper">
		<!--  -->
		<div class="mine-scroll-pull-down" v-if="pullDown">
			<me-loading :text="pullDownText" inline ref="pullDownLoading" />
		</div>
		<swiper-slide>
			<slot></slot>
		</swiper-slide>
		<!--  -->
		<div class="mine-scroll-pull-up" v-if="pullUp">
			<me-loading :text="pullUpText" inline ref="pullUpLoading" />
		</div>
		<!--  swiperscrollbar -->
		<div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
	</swiper>
</template>

<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper';
	import MeLoading from 'base/loading';
	import {
		// 
		PULL_DOWN_HEIGHT,
		PULL_DOWN_TEXT_INIT,
		PULL_DOWN_TEXT_START,
		PULL_DOWN_TEXT_ING,
		PULL_DOWN_TEXT_END,
		// 
		PULL_UP_HEIGHT,
		PULL_UP_TEXT_INIT,
		PULL_UP_TEXT_START,
		PULL_UP_TEXT_ING,
		PULL_UP_TEXT_END
	} from './config';

	export default {
		name: 'MeScroll',
		components: {
			swiper,
			swiperSlide,
			MeLoading
		},
		props: {
			scrollbar: {
				type: Boolean,
				default: true
			},
			// home index.vue 
			// <me-scroll :data="recommends">
			data: {
				type: [Array, Object]
			},
			pullDown: {
				type: Boolean,
				default: false
			},
			pullUp: {
				type: Boolean,
				default: false
			}
		},

		// data() {
		// 	return {

		// 	};
		// },

		// 
		watch: {
			data() {
				this.update();
			}
		},
		// init created
		created() {
			this.init();
		},

		methods: {
			// 
			update() {
				// swiperapi
				// <swiper :options="swiperOption" ref="swiper">
				// ref
				// console.log(this.$refs.swiper);
				// this.$refs.swiper && this.$refs.swiper.swiper.update();
				// ,,dom
				this.$nextTick(() => {
					this.$refs.swiper && this.$refs.swiper.swiper.update();
				})
			},
			//  api
			scrollToTop(speed, runCallbacks) {
				this.$refs.swiper &&
					this.$refs.swiper.swiper.slideTo(0, speed, runCallbacks);
			},
			// 
			init() {
				this.pulling = false;
				this.pullDownText = PULL_DOWN_TEXT_INIT;
				this.pullUpText = PULL_DOWN_TEXT_INIT;
				this.swiperOption = {
					direction: 'vertical',
					slidesPerView: 'auto',
					freeMode: true,
					setWrapperSize: true,
					scrollbar: {
						el: this.scrollbar ? '.swiper-scrollbar' : null,
						hide: true
					},
					// swiper-slide
					on: {
						// ---- scroll 
						sliderMove: this.scroll,
						// --- touchEnd methods
						touchEnd: this.touchEnd,
						// 
						transitionEnd: this.scrollEnd
					}
				};
			},
			// scroll  --
			scroll() {
				// this.$refs.swiper.swiper swiper
				// swiper
				const swiper = this.$refs.swiper.swiper;

				// scroll translate swiper
				//  
				this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper);

				if (this.pulling) {
					return;
				}

				//  
				if (swiper.translate > 0) {
					if (!this.pullDown) {
						// 
						return;
					}
					if (swiper.translate > PULL_DOWN_HEIGHT) {
						this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
					} else {
						this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);
					}
				} else if (swiper.isEnd) {
					// 
					if (!this.pulling) {
						return;
					}
					// 
					// Math.abs
					// swiper.$warpperEl
					// cssswiper CSS
					const isPullUp =
						Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT >
						parseInt(swiper.$wrapperEl.css('height'));

					if (isPullUp) {
						this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
					} else {
						this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
					}
				}
			},
			// 
			scrollEnd() {
				this.$emit(
					'scroll-end',
					this.$refs.swiper.swiper.translate,
					this.$refs.swiper.swiper,
					// pullinghomeindex
					this.pulling
				);
			},
			// 
			touchEnd() {
				if (this.pulling) {
					return;
				}

				const swiper = this.$refs.swiper.swiper;

				//  
				if (swiper.translate > PULL_DOWN_HEIGHT) {
					// 
					if (!this.pullDown) {
						return;
					}

					this.pulling = true;
					// swiperapi
					swiper.allowTouchMove = false; // 
					swiper.setTransition(swiper.params.speed);
					swiper.setTranslate(PULL_DOWN_HEIGHT);
					swiper.params.virtualTranslate = true; // 
					this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING);
					//   emit
					// pull-downhome_index
					this.$emit('pull-down', this.pullDownEnd);
				}

				//    else if
				//  
				else if (swiper.isEnd) {
					const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
					const isPullUp =
						Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT >
						totalHeight;
					// 
					if (isPullUp) {
						if (!this.pullUp) {
							return;
						}
						this.pulling = true;
						swiper.allowTouchMove = false; // 
						swiper.setTransition(swiper.params.speed); // 
						swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height)); // 
						swiper.params.virtualTranslate = true; // 
						this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING); // 
						this.$emit('pull-up', this.pullUpEnd); // 
					}
				}
			},
			// this.$emit('pull-down', this.pullDownEnd);
			// pullDownEnd 
			pullDownEnd() {
				const swiper = this.$refs.swiper.swiper;

				this.pulling = false;
				this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END);
				swiper.params.virtualTranslate = false;
				swiper.allowTouchMove = true;
				swiper.setTransition(swiper.params.speed);
				swiper.setTranslate(0);
				// ,header
				setTimeout(() => {
					this.$emit('pull-down-transition-end');
				}, swiper.params.speed);
			},
			// this.$emit('pull-up', this.pullUpEnd);
			// pullUpEnd 
			pullUpEnd() {
				const swiper = this.$refs.swiper.swiper;

				this.pulling = false;
				this.$refs.pullDownLoading.setText(PULL_UP_TEXT_END);
				swiper.params.virtualTranslate = false;
				swiper.allowTouchMove = true;
			}
		}
	};
</script>

<style lang='scss' scoped>
	.swiper-container {
		width: 100%;
		height: 100%;
	}

	.swiper-slide {
		height: auto;
	}

	//  
	.mine-scroll-pull-down {
		position: absolute;
		left: 0;
		bottom: 100%;
		width: 100%;
		height: 80px;
	}

	// 
	.mine-scroll-pull-down,
	.mine-scroll-pull-up {
		position: absolute;
		left: 0;
		width: 100%;
	}

	.mine-scroll-pull-down {
		bottom: 100%;
		height: 80px;
	}

	.mine-scroll-pull-up {
		top: 100%;
		height: 30px;
	}
</style>


